export default {
    data() {
        return {

            data: [],

            // 当前点击选中的筛选类目
            filterIndex: "-1",

            // 是否展开筛选项
            filterShow: false,

            //已选中的项
            filterSelect: {
            }
        }
    },
    // filterData:所有的数据(必填参数)   currenSelect:已选在数据(可选参数)
    props: ['filterData', 'currenSelect'],
    mounted() {
        let total = this.filterData;
        if (total) {
            this.data = this.filterData;
            // 初始化默认选中的选项
            total.forEach(element => {
                let key = element['key'];
                console.log(key);
                let value = element['options'] ? element['options'][0]["key"]: "";
                this.filterSelect[key] = value;
            });
        }
    },
    methods: {
        // 筛选条目被点击
        itemClicked(k) {
            this.filterIndex = k;
            if (!this.filterShow) {
                this.filterShow = true;
            }
        },

        // 选中了某一个选项
        selectItem(k, kk) {
            this.filterSelect[k] = kk;
            this.$emit("select", this.filterSelect);
            this.filterShow = false;
            this.filterIndex = "-1";
        }

    }
}
